<template>
  <transition name="list-fade">
    <div class="playlist" v-show="showFlag" @click="hide">
      <div class="list-wrapper" @click.stop>
        <div class="list-header">
          <h1 class="title">
            <i class="icon" :class="modeClass" @click="changeMode"></i>
            <span class="text">{{modeText}}</span>
            <span class="clear" @click.stop="showConfirm">
              <i class="icon-clear"></i>
            </span>
          </h1>
        </div>
        <scroll :refreshDelay="refreshDelay" ref="scroll" :data="sequenceList" class="list-content">
          <transition-group name="list" tag="ul">
            <li ref="listBox" @click="selectItem(item,index)" :key="item.id" v-for="(item,index) in sequenceList" class="item">
              <i @click.stop="togglePlaying" :class="getCurrentIcon(item)" class="current"></i>
              <span class="text" :class="getCurrentText(item)">{{index+1}}-{{item.name}}</span>
              <span class="like" @click.stop="toggleFavorite(item)">
                <i :class="getFavoriteIcon(item)"></i>
              </span>
              <span class="delete" @click.stop="deleteOne(item)">
                <i class="icon-delete"></i>
              </span>
            </li>
          </transition-group>
        </scroll>
        <div class="list-operate">
          <div class="add" @click="addSong">
            <i class="icon-add"></i>
            <span class="text">添加歌曲到列表</span>
          </div>
        </div>
        <div class="list-close" @click="hide">
          <span>关闭</span>
        </div>
      </div>
      <confirm ref="confirm" @confirm="emptyList" text="确认清空播放列表？" confirmBtnText="清空"></confirm>
      <add-song ref="addSong"></add-song>
    </div>
  </transition>
</template>

<script>
import {mapActions} from 'vuex'
import Scroll from 'base/scroll/scroll'
import Confirm from 'base/confirm/confirm'
import AddSong from 'components/add-song/add-song'
import {playMode} from 'common/js/config'
import {playerModeMixin} from 'common/js/mixin'

export default {
  mixins: [playerModeMixin],
  data() {
    return {
      showFlag: false,
      refreshDelay: 100
    }
  },
  components: {
    Scroll,
    Confirm,
    AddSong
  },
  computed: {
    modeText() {
      return this.mode === playMode.random ? '随机播放' : this.mode === playMode.sequence ? '顺序播放' : '单曲循环'
    }
  },
  methods: {
    addSong() {
      this.$refs.addSong.show()
    },
    showConfirm() {
      this.$refs.confirm.show()
    },
    emptyList() {
      this.emptyPlaylist()
      this.hide()
    },
    deleteOne(item) {
      this.deleteSong(item)
      if (!this.playList.length) {
        this.hide()
      }
    },
    // 播放暂停
    togglePlaying() {
      this.setPlayingState(!this.playing)
    },
    // 选择播放列表中的歌曲
    selectItem(item, index) {
      // 随机模式下 歌曲的index
      if (this.mode === playMode.random) {
        index = this.playList.findIndex((song) => {
          return item.id === song.id
        })
      }
      this.setCurrentIndex(index)
      // 播放模式
      this.setPlayingState(true)
    },
    // 播放列表自动滚动到当前播放歌曲 中间位置
    scrollToCurrent(current) {
      const index = this.sequenceList.findIndex((song) => {
        return song.id === current.id
      })
      // 滚动到中间位置
      let newIndex = index >= 3 ? index - 3 : -1
      // console.log(newIndex)
      this.$refs.scroll.scrollToElement(this.$refs.listBox[newIndex], 300)
    },
    // 判断是否正在播放
    getCurrentIcon(item) {
      return this.currentSong.id === item.id ? this.playing ? 'icon-pause' : 'icon-play' : ''
    },
    getCurrentText(item) {
      return this.currentSong.id === item.id ? 'playing-text' : ''
    },
    show() {
      this.showFlag = true
      // 确保能够滚动
      setTimeout(() => {
        this.$refs.scroll.refresh()
        this.scrollToCurrent(this.currentSong)
      }, 20)
    },
    hide() {
      this.showFlag = false
    },
    ...mapActions([
      'deleteSong',
      'emptyPlaylist'
    ])
  },
  watch: {
    // 当前歌曲变化时  滚动
    currentSong(newSong, oldSong) {
      // 或者当前列表为空 直接返回
      if (newSong.id === oldSong.id || !this.showFlag) {
        return
      }
      this.scrollToCurrent(newSong)
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .playlist
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 200
    background-color: $color-background-d
    &.list-fade-enter-active, &.list-fade-leave-active
      transition: opacity 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        transform: translate3d(0, 100%, 0)
    &.list-fade-enter
    .list-wrapper
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      background-color: $color-highlight-background
      .list-header
        position: relative
        padding: 20px 30px 10px 20px
        .title
          display: flex
          align-items: center
          .icon
            margin-right: 10px
            font-size: 30px
            color: $color-theme-d
          .text
            flex: 1
            font-size: $font-size-medium
            color: $color-text-l
          .clear
            extend-click()
            .icon-clear
              font-size: $font-size-medium
              color: $color-text-d
      .list-content
        max-height: 240px
        overflow: hidden
        .item
          display: flex
          align-items: center
          height: 40px
          padding: 0 30px 0 20px
          overflow: hidden
          &.list-enter-active, &.list-leave-active
            transition: all 0.1s
          &.list-enter, &.list-leave-to
            height: 0
          .current
            flex: 0 0 20px
            width: 20px
            font-size: $font-size-small
            color: $color-theme-playing
          .text
            flex: 1
            no-wrap()
            font-size: $font-size-medium
            color: $color-text-d
            &.playing-text
              color #fff
          .like
            extend-click()
            margin-right: 15px
            font-size: $font-size-small
            color: $color-theme
            .icon-favorite
              color: $color-sub-theme
          .delete
            extend-click()
            font-size: $font-size-small
            color: $color-theme
      .list-operate
        width: 140px
        margin: 20px auto 30px auto
        .add
          display: flex
          align-items: center
          padding: 8px 16px
          border: 1px solid $color-text-l
          border-radius: 100px
          color: $color-text-l
          .icon-add
            margin-right: 5px
            font-size: $font-size-small-s
          .text
            font-size: $font-size-small
      .list-close
        text-align: center
        line-height: 50px
        background: $color-background
        font-size: $font-size-medium-x
        color: $color-text-l
</style>



